import React from 'react';
import { Box, Card, CardContent, Typography, Avatar, Grid, useTheme } from '@mui/material';
import { Code, Person, Info } from '@mui/icons-material';

const CreditsPage = () => {
  const theme = useTheme();
  const isDark = theme.palette.mode === 'dark';

  const cardStyles = {
    height: '100%',
    transition: 'transform 0.3s, box-shadow 0.3s',
    '&:hover': {
      transform: 'translateY(-5px)',
      boxShadow: theme.shadows[8],
    },
    backgroundColor: theme.palette.background.paper,
  };

  const iconStyles = {
    fontSize: 40,
    color: theme.palette.primary.main,
    mb: 2,
  };

  return (
    <Box sx={{ p: 3 }}>
      <Typography variant="h4" component="h1" gutterBottom sx={{ 
        fontWeight: 700,
        mb: 4,
        color: theme.palette.text.primary,
      }}>
        Project Credits
      </Typography>

      <Grid container spacing={4}>
        {/* Author Card */}
        <Grid item xs={12} md={4}>
          <Card sx={cardStyles}>
            <CardContent sx={{ textAlign: 'center', py: 4 }}>
              <Person sx={iconStyles} />
              <Typography variant="h6" component="h2" gutterBottom sx={{ fontWeight: 600 }}>
                Author
              </Typography>
              <Avatar 
                alt="Author" 
                src="/path-to-your-avatar.jpg" 
                sx={{ 
                  width: 80, 
                  height: 80, 
                  mx: 'auto',
                  mb: 2,
                  border: `2px solid ${theme.palette.primary.main}`
                }} 
              />
              <Typography variant="body1" gutterBottom sx={{ color: theme.palette.text.secondary }}>
                Your Name
              </Typography>
              <Typography variant="body2" sx={{ color: theme.palette.text.secondary }}>
                Role/Position
              </Typography>
              <Typography variant="body2" sx={{ mt: 2, color: theme.palette.text.secondary }}>
                Contact: your.email@example.com
              </Typography>
            </CardContent>
          </Card>
        </Grid>

        {/* About the Project Card */}
        <Grid item xs={12} md={4}>
          <Card sx={cardStyles}>
            <CardContent sx={{ textAlign: 'center', py: 4 }}>
              <Info sx={iconStyles} />
              <Typography variant="h6" component="h2" gutterBottom sx={{ fontWeight: 600 }}>
                About the Project
              </Typography>
              <Typography variant="body1" sx={{ 
                color: theme.palette.text.secondary,
                mb: 2,
              }}>
                This project visualizes environmental data with interactive maps and real-time analytics.
              </Typography>
              <Typography variant="body2" sx={{ color: theme.palette.text.secondary }}>
                <strong>Version:</strong> 1.0.0
              </Typography>
              <Typography variant="body2" sx={{ color: theme.palette.text.secondary }}>
                <strong>Last Updated:</strong> {new Date().toLocaleDateString()}
              </Typography>
              <Typography variant="body2" sx={{ mt: 2, color: theme.palette.text.secondary }}>
                Built with ❤️ for better environmental awareness
              </Typography>
            </CardContent>
          </Card>
        </Grid>

        {/* Frameworks Used Card */}
        <Grid item xs={12} md={4}>
          <Card sx={cardStyles}>
            <CardContent sx={{ textAlign: 'center', py: 4 }}>
              <Code sx={iconStyles} />
              <Typography variant="h6" component="h2" gutterBottom sx={{ fontWeight: 600 }}>
                Frameworks Used
              </Typography>
              <Box sx={{ 
                display: 'flex',
                flexDirection: 'column',
                gap: 1,
                alignItems: 'flex-start',
                pl: 4,
                color: theme.palette.text.secondary,
              }}>
                <Typography variant="body1">• React</Typography>
                <Typography variant="body1">• Material-UI</Typography>
                <Typography variant="body1">• Leaflet.js</Typography>
                <Typography variant="body1">• Next.js</Typography>
                <Typography variant="body1">• Chart.js</Typography>
              </Box>
              <Typography variant="body2" sx={{ 
                mt: 3,
                fontStyle: 'italic',
                color: theme.palette.text.secondary,
              }}>
                Special thanks to all open-source contributors
              </Typography>
            </CardContent>
          </Card>
        </Grid>
      </Grid>
    </Box>
  );
};

export default CreditsPage;